a {
  text-decoration: none;
}

header {
  display: flex;
  margin-top: 50px;
  justify-content: space-between;
  border: 1px solid red;
  padding: 0 100px;
a {
  font-size: 24px;
  font-family: "Roboto", sans-serif;
  color: rgb(34, 34, 34);
  //line-height: 0.908;

}
}
.logo {
  display: flex;
  align-items: center;
  p {
    margin-left: 35px;
    font-size: 18px;
    font-family: "Roboto", sans-serif;
    color: rgb(153, 153, 153);
    //line-height: 1.5;
  }
}

.header__tel {
  a {
    line-height: 2;
  }
  p {
    font-size: 20px;
    font-family: "Roboto", sans-serif;
    color: rgb(153, 153, 153);
    line-height: 1.09;
    padding: 0;
    margin: 0;

  }

}
